import { CreateCameraVideo, TakePhoto } from "@/AR-Renderer/camera_video";
import { TargetScene } from "@/AR-Renderer/scenes/target_scene";
import { FaceScene } from "./scenes/face_scene";
import { GenerateTarget } from "@/AR-Renderer/target_generator";


class SceneLoader {
    constructor(unityContainer, videoContainer, vue) {
        this.unityContainer = unityContainer;
        this.videoContainer = videoContainer;

        this.mindARContainerId = vue.mindARContainerId;
        this.showLoadingIndicator = vue.ShowLoadingIndicator;
        this.showScanningIndicator = vue.ShowScanningIndicator;

        this.video = null;

        this.scenes = {};

        this.faceScene = null;
        this.targetScene = null;

        this.currentScene = null;
        window.SCENE_LOADER = this;
    }
        //targetFileOrImg Unused for now
    async LoadScene(sceneType, sceneIndex, controller, amountOfTargetsAsStr) {
        this.showLoadingIndicator(true);

        let sceneData = {
            sceneType: sceneType,
            sceneIndex: Number(sceneIndex),
            controller: controller
        };

        switch (sceneType) {
            case "face":
                break;
            case "image":
                sceneData.targets = null;
                sceneData.amountOfTargets = Number(amountOfTargetsAsStr);

                break;

            case "surface":
                sceneData.targets = null;
                sceneData.targetImg = null;
                sceneData.amountOfTargets = Number(amountOfTargetsAsStr);

                break;

            default:
                throw new Error(`${sceneType} is not correct scene type!`);
        }

        if (!this.scenes[sceneData.sceneIndex]) {
            this.scenes[sceneData.sceneIndex] = sceneData;
        }

        this.DisplayScene(this.scenes[sceneData.sceneIndex]);
    }

    async DisplayScene(sceneData) {
        if (this.currentScene !== null) {
            switch (this.currentScene.sceneType) {
                case "face":
                    if (this.faceScene?.stop) {
                        await this.faceScene.stop();
                    }
                    break;


                case "surface":
                case "image":
                    if (this.targetScene?.stop) {
                        await this.targetScene.stop();
                    }
                    break;

                default:
            }
        }

        this.currentScene = sceneData;

        let scene;
        switch (sceneData.sceneType) {
            case "face":
                scene = this.faceScene;
                if (!scene) {
                   scene = await this.CreateScene(this.currentScene);
                }

                scene.Initialize();

                break;

            case "image":
                scene = this.targetScene;

                if (scene) {
                    scene.Initialize(`./Targets/targets_${this.currentScene.sceneIndex}.mind`);
                } else {
                    scene = await this.CreateScene(this.currentScene);
                }
                break;

            case "surface":
                if (this.currentScene.targetImg === null) {
                    this.SetupSurfaceRecognition();
                    this.showLoadingIndicator(false);
                    return;
                }

                scene = this.targetScene;

                if (scene) {

                    if (this.currentScene.targets === null) {
                        await this.GenerateTargetForScene(this.currentScene);
                    }

                    scene.Initialize(this.currentScene.targets);
                } else {
                    scene = await this.CreateScene(this.currentScene);
                }

                break;

            default:
        }

        this.StartScene(scene);
    }

    async StartScene(scene) {
        if (scene.start) {
            if (scene.mindARContainer) {
                this.unityContainer.parentElement.appendChild(scene.mindARContainer);
                await scene.start();
                await setTimeout(() => { return }, 200);
                this.videoContainer.style.display = 'none';
                this.showScanningIndicator(false);
                this.showLoadingIndicator(false);
            }
        }
    }

    async ReloadScene() {
        if (this.currentScene !== null) {
            this.showLoadingIndicator(true);

            let scene;

            switch (this.currentScene.sceneType) {
                case "face":
                    scene = this.faceScene;
                    break;

                case "surface":
                case "image":
                    scene = this.targetScene;
                    break;
            }

            await scene.stop();
            this.StartScene(scene);
        }
    }

    async CreateScene(sceneData) {
        let mindARContainer = this.CreateMindArContainer();
        let scene;

        switch (sceneData.sceneType) {
            case "face":
                mindARContainer.style.transform = 'scale(-1, 1)';

                scene = new FaceScene(sceneData.sceneIndex, sceneData.controller);

                this.faceScene = scene;

                await scene.CreateMindAr(mindARContainer);
                return scene;

            case "image":
                mindARContainer.style.transform = 'scale(1, 1)';

                scene = new TargetScene(sceneData.sceneIndex, sceneData.controller);
                scene.Initialize(`./Targets/targets_${sceneData.sceneIndex}.mind`);

                this.targetScene = scene;
                await scene.CreateMindAr(mindARContainer, sceneData.amountOfTargets);
                return scene;

            case "surface":
                mindARContainer.style.transform = 'scale(1, 1)';

                scene = new TargetScene(sceneData.sceneIndex, sceneData.controller);

                if (sceneData.targets === null) {
                    await this.GenerateTargetForScene(sceneData);
                }

                scene.Initialize(sceneData.targets);

                this.targetScene = scene;

                await scene.CreateMindAr(mindARContainer, sceneData.amountOfTargets);
                return scene;

            default:
                //Invalid scene type
        }
    }

    async SetupSurfaceRecognition() {
        this.videoContainer.style.transform = 'scale(1, 1)';
        this.videoContainer.style.display = 'block';

        if (this.video == null) {
            let video = await CreateCameraVideo('environment');
            this.video = video;
            this.videoContainer.appendChild(this.video);
        }
    }

    async GenerateTargetForScene(sceneData) {
        let targets;
        if (sceneData.sceneType === "surface") {
            targets = await GenerateTarget([sceneData.targetImg]);
        } else {
            targets = await GenerateTarget([`./Targets/${sceneData.targetImg}.png`]);
        }

        sceneData.targets = targets;
        return;
    }

    //Call from unity
    async ScanSurface(sceneIndex) {
        sceneIndex = Number(sceneIndex);
        if (this.currentScene.sceneIndex === sceneIndex) {
            this.showScanningIndicator(true);
            this.currentScene.targetImg = TakePhoto(this.video);
            await this.GenerateTargetForScene(this.currentScene);
            this.DisplayScene(this.currentScene);
        }
    }

    CreateMindArContainer() {
        let mindARContainer = document.createElement("div");
        mindARContainer.setAttribute("id", "mindArContainer");
        mindARContainer.setAttribute("class", "fixed");
        return mindARContainer;
    }
}



export { SceneLoader };
