import { Scene } from "@/AR-Renderer/scenes/scene";
import { MindArFace } from "@/AR-Renderer/mind_ar";
import * as AR_Renderer from "@/AR-Renderer/ar_renderer";

class FaceScene extends Scene {
    constructor(sceneIndex, controller) {
        super(sceneIndex, controller);
    }

    Initialize() {
        return;
    }

    async CreateMindAr(mindARContainer) {
        this.mindARContainer = mindARContainer;
        return await import("mind-ar/dist/mindar-face-three.prod.js").then(async () => {
            let mindAr = MindArFace(this.mindARContainer = mindARContainer);
            this.mindAr = mindAr;
            let faceMesh = mindAr.addFaceMesh();

            mindAr.addAnchor(0);

            let renderer = mindAr.renderer;
            let scene = mindAr.scene;
            let camera = mindAr.camera;

            this.start = async () => {
                await mindAr.start();

                renderer.setAnimationLoop(() => {
                    renderer.render(scene, camera);
                    this.CheckTarget(mindAr, faceMesh.geometry.positions);
                });

                AR_Renderer.onSceneLoaded(this.controller, Number(this.sceneIndex));
                return;
            }
            this.stop = async () => {
                AR_Renderer.onFaceLost(this.controller);
                await mindAr.stop();
                this.mindARContainer.remove();
                renderer.setAnimationLoop(null);
                return;
            }

            return;
        });
    }

    CheckTarget(mindAr, faceMesh) {
        let aspect = mindAr.container.offsetWidth / mindAr.container.offsetHeight;

        let cameraFOV = aspect < (4 / 3) ? mindAr.camera.fov : mindAr.camera.fov * (4 / 3) / aspect;
        let sceneAnchor = mindAr.anchors[0];

        if (sceneAnchor.group.visible) {
            let matrixElements = sceneAnchor.group.matrix.elements;
            if (!this.isTargetVisible) {
                this.isTargetVisible = true;
                AR_Renderer.onFaceFound(this.controller);
            }
            AR_Renderer.onFaceTracking(matrixElements, cameraFOV, faceMesh);
        } else {
            if (this.isTargetVisible) {
                this.isTargetVisible = false;
                AR_Renderer.onFaceLost(this.controller);
            }
        }
    }
}

export { FaceScene };