import { Scene } from "@/AR-Renderer/scenes/scene";
import { MindArTarget } from "@/AR-Renderer/mind_ar";
import * as AR_Renderer from "@/AR-Renderer/ar_renderer";
import { OnTargetLost, OnTargetTracking } from "../bridge";

class TargetScene extends Scene {
    constructor(sceneIndex, controller) {
        super(sceneIndex, controller);
        this.targets = null;
        this.currentTracking = -1;
    }

    Initialize(targets) {
        this.targets = targets;
        return;
    }

    async CreateMindAr(mindARContainer, amountOfTargets) {
        this.mindARContainer = mindARContainer;
        return await import("mind-ar/dist/mindar-image-three.prod.js").then(async () => {
            let mindAr = MindArTarget(this.targets, this.mindARContainer);
            this.mindAr = mindAr;

            for (var i = 0; i < amountOfTargets; i++) {
                mindAr.addAnchor(i);
            }

            let renderer = mindAr.renderer;
            let scene = mindAr.scene;
            let camera = mindAr.camera;

            this.start = async () => {
                await mindAr.start();

                for (var i = 0; i < amountOfTargets; i++) {
                    mindAr.anchors[i].group.visible = false;
                }

                let targetsStatusM = {};
                renderer.setAnimationLoop(() => {
                    renderer.render(scene, camera);
                    const targetsStatusP = {};
                    for (var i = 0; i < amountOfTargets; i++) {
                        targetsStatusP[i] = this.CheckTarget(mindAr, i);
                    }

                    for (const [targetIndex, targetData] of Object.entries(targetsStatusP)) { 
                        if (targetsStatusM[targetIndex] == null && targetData != null) {
                            //Found
                            if (this.currentTracking == -1) {
                                this.currentTracking = targetIndex;
                                AR_Renderer.onTargetFound(this.controller, this.currentTracking);
                            } else {
                                AR_Renderer.onTargetLost(this.controller, this.currentTracking);
                                this.currentTracking = targetIndex;
                                AR_Renderer.onTargetFound(this.controller, this.currentTracking);
                            }
                        }
                        if (targetsStatusM[targetIndex] != null && targetData == null) {
                            //Lost
                            if (this.currentTracking == targetIndex) {
                                AR_Renderer.onTargetLost(this.controller, this.currentTracking);
                                this.currentTracking = -1;
                            }
                        }

                        if (targetsStatusM[targetIndex] != null && targetData != null) {
                            //Tracking 
                            if (this.currentTracking == targetIndex) {
                                AR_Renderer.onTargetTracking(targetData.matrixElements, targetData.cameraFOV, this.currentTracking);
                            } 
                        }
                    }

                    targetsStatusM = targetsStatusP;
                });

                AR_Renderer.onSceneLoaded(this.controller, Number(this.sceneIndex));
                return;
            }
            this.stop = async () => {
                AR_Renderer.onTargetLost(this.controller);
                await mindAr.stop();
                this.mindARContainer.remove();
                renderer.setAnimationLoop(null);
                return;
            }
            return;
        });
    }

    CheckTarget(mindAr, targetIndex) {
        let aspect = mindAr.container.offsetWidth / mindAr.container.offsetHeight;

        let cameraFOV = aspect < (4 / 3) ? mindAr.camera.fov : mindAr.camera.fov * (4 / 3) / aspect;
        let sceneAnchor = mindAr.anchors[targetIndex];

        if (sceneAnchor.group.visible) {
            let matrixElements = sceneAnchor.group.matrix.elements;
            return {
                matrixElements: matrixElements,
                cameraFOV: cameraFOV,
                targetIndex: targetIndex,
            };
        } else {
            return null;
        }
    }
}

export { TargetScene };