import { Matrix4x4 } from "@/AR-Renderer/math/matrix4x4";
import { Vector3 } from "@/AR-Renderer/math/vector3";
import { Quaternion } from "@/AR-Renderer/math/quaternion";
import * as Bridge from "./bridge";

let UnityInstance;


export function Init(canvas, callback) {
    var buildUrl = `./Build`;

    //DEBUG
    const DEBUG_MODE = (process.env.NODE_ENV === "production" ? false : true);

    var loaderUrl;
    var config;

    if (DEBUG_MODE) {
        loaderUrl = buildUrl + "/Build/Build.loader.js";
        config = {
            dataUrl: buildUrl + "/Build/Build.data",
            frameworkUrl: buildUrl + "/Build/Build.framework.js",
            codeUrl: buildUrl + "/Build/Build.wasm",
            streamingAssetsUrl: "StreamingAssets",
            companyName: "{{{ COMPANY_NAME }}}",
            productName: "{{{ PRODUCT_NAME }}}",
            productVersion: "{{{ PRODUCT_VERSION }}}",
        };
    } else {
        loaderUrl = buildUrl + "/{{{LOADER_FILENAME}}}";
        config = {
            dataUrl: buildUrl + "/{{{DATA_FILENAME}}}",
            frameworkUrl: buildUrl + "/{{{FRAMEWORK_FILENAME}}}",
            codeUrl: buildUrl + "/{{{CODE_FILENAME}}}",
            streamingAssetsUrl: "StreamingAssets",
            companyName: "{{{ COMPANY_NAME }}}",
            productName: "{{{ PRODUCT_NAME }}}",
            productVersion: "{{{ PRODUCT_VERSION }}}",
        };
    }

    canvas.width = window.innerWidth;
    canvas.height = window.innerHeight;

    var script = document.createElement("script");
    script.src = loaderUrl;
    script.onload = () => {
        // eslint-disable-next-line no-undef
        createUnityInstance(canvas, config)
            .then((unityInstance) => {
                UnityInstance = unityInstance;
                Bridge.Init_functions(unityInstance);
                callback(unityInstance);
            })
            .catch((message) => {
                //Handle error
            });
    };
    canvas.appendChild(script);
}


//Image and Surface
export function onTargetFound(controller, targetIndex) {
    Bridge.OnTargetFound(controller, Number(targetIndex), UnityInstance);
}

export function onTargetTracking(sceneMatrixElements, cameraFOV, targetIndex) {
    DecomposeSceneMatrix(sceneMatrixElements, (position, rotation, scale) => {
        Bridge.OnTargetTracking(UnityInstance, position, rotation, scale, cameraFOV, targetIndex);
    });
}

export function onTargetLost(controller, targetIndex) {
    Bridge.OnTargetLost(controller, Number(targetIndex), UnityInstance);
}
///



//Face
export function onFaceFound(controller) {
    Bridge.OnFaceFound(controller, UnityInstance);
}

export function onFaceTracking(sceneMatrixElements, cameraFOV, faceMeshData) {
    DecomposeSceneMatrix(sceneMatrixElements, (position, rotation, scale) => {
        Bridge.OnFaceTracking(UnityInstance, position, rotation, scale, cameraFOV, faceMeshData);
    });
}

export function onFaceLost(controller) {
    Bridge.OnFaceLost(controller, UnityInstance);
}
///


//Uni
export function onSceneLoaded(controller, sceneIndex) {
    Bridge.OnSceneLoaded(controller, UnityInstance, sceneIndex);
}



function DecomposeSceneMatrix(sceneMatrixElements, callback) {
    const matrix = new Matrix4x4(sceneMatrixElements);

    const position = new Vector3();
    const rotation = new Quaternion();
    const scale = new Vector3();

    matrix.decompose(position, rotation, scale);
    return callback(position, rotation, scale);
}